body{overflow:hidden;margin:0px}#demo-container{background-color:rgba(0,0,0,0.05);height:100vh;top:0px;right:0px;width:100vw;align-items:center;justify-content:center}#render-section{overflow:hidden;box-sizing:border-box;width:100% !important;height:100%  !important;position:relative;top:0px;display:flex;align-items:center;justify-content:center;box-sizing:border-box;border-radius:0px}#render-section canvas,#render-section svg{position:absolute;top:0px;left:0px;z-index:-1;height:100%;width:100%}#info-section{border:2px solid black;width:200px;margin-left:50px;height:300px;background-color:white;position:absolute;right:50px;top:20vh;border-radius:8px;position:fixed;z-index:10;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}#info-section .algorithms-container{display:flex;flex-direction:column;align-items:center;justify-content:center}
